export default {
    syncGetSystemInfo(context) {
        context.commit('GetSystemInfo')
    },
    syncGetProductParameter(context,data) {
        context.commit('GetProductParameter',data)
    },
    syncCreateProductParameter(context,data) {
        context.commit('CreateProductParameter',data)
        context.commit('GetProductParameter',data)
    },
    syncSetProductParameter(context,data) {
        context.commit('SetProductParameter',data)
        context.commit('GetProductParameter',data)
    }
}