import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'
import store from '@/store'

const routes = [
  {
    path: '/',
    name: 'Home',
    meta: {
      title: '首页',
    },
    component: Home
  },
  {
    path: '/about',
    name: 'about',
    meta: {
      title: '关于',
    },
    component: () => import('../views/About.vue')
  },
  {
    path: '/shejitu',
    name: 'shejitu',
    meta: {
      title: '设计图列表',
    },
    component: () => import('@/views/sucaiguanli/shejituliebiao/liebiao')
  },
  {
    path: '/banxing-list',
    name: 'banxinglist',
    meta: {
      title: '版型列表',
    },
    component: () => import('@/views/banxingguanli/banxingliebiao/liebiao')
  },
  {
    path: '/banxing-handle',
    name: 'banxinghandle',
    meta: {
      title: '版型管理',
    },
    component: () => import('@/views/sucaiguanli/shejituliebiao/liebiao')
  },
  {
    path: '/parameter',
    name: 'parameter',
    meta: {
      title: '参数设置',
    },
    component: () => import('@/views/Parameter/Parameter')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to,from)=>{
  return to.matched.length !== 0;
})
router.afterEach((to,from,failure)=>{
  if (!failure) store.commit('SetMainTitleText',{title:to.meta.title})
})

export default router
