import {getshejitulist} from "@/netword/sucai";

export default {
  // 方法
  SetPage(state, newval) {
    state.Page = newval
  },
  SetPageSize(state, newval) {
    state.PageSize = newval
  },
  SetUploadDate(state, newval) {
    state.UploadDate = newval
  },
  SetSort(state, newval) {
    state.ListSort = newval
  },
  GetImgList(state){
    state.Imgs = {}
    state.Listloading = true
    const cof = {
      page:state.Page,
      pagesize:state.PageSize,
      sort:state.ListSort
    }
    getshejitulist(cof).then(res => {
      state.Imgs = res.data.imgs
      state.Count = res.data.count
      state.Listloading = false
    })
  }
}
