import { GetSystemInfo } from "@/netword/system";
import { CreateParameter, GetParameter, SetParameter } from "@/netword/parameter";
import Success from "@/common/RequestResult/axios/Success";
import Error from "@/common/RequestResult/axios/Error";
import { ElMessage } from "element-plus";

export default {
  // 方法
  SetMainTitleText(state, payload) {
    state.MainTitleText = payload.title
  },
  GetSystemInfo(state) {
    GetSystemInfo().then(res => {
      state.systemInfo = res.data
    })
  },
  GetProductParameter(state,data) {
    GetParameter(data).then((req)=>{
      if (req.code==="200") {
        state.systemInfo.pruductParameter[data.cls].data = req.data
      } else {
        ElMessage({message:req.message,type:'error'})
      }}).catch(Error)
  },
  CreateProductParameter(state,data) {
    CreateParameter(data).then(Success).catch(Error);
  },
  SetProductParameter(state,data) {
    SetParameter(data).then(Success).catch(Error);
  }
}
