          <span class="percentage-label">系统空间</span>
        </template>
    </el-progress>
    </div>
  </div>
</template>

<script>
export default {
  name: "index",
  data: () => {return {
    customColors: [
      { color: '#5cb87a', percentage: 70 },
      { color: '#e6a23c', percentage: 90 },
      { color: '#f56c6c', percentage: 100 },
    ]
  }}
}
</script>

