import {getshejitulist} from "@/netword/sucai";
import LoadList from "@/store/modules/banxing/info/Load";

export default {
  // 方法
  SetPage(state, newval) {
    state.Page = newval
  },
  SetSelected(state, newval) {
    state.LoadList.ReqData.Selected[newval.class] = newval.data
  },
  SetDateSelect(state, newval) {
    state.LoadList.ReqData.Selected.DateSelect = newval
    state.LoadList.Selected.DateSelect.info = newval
  },
  SetSort(state, newval) {
    state.LoadList.ReqData.Selected.Sort = newval
    state.LoadList.Selected.Sort.info = newval
  },
  GetImgList(state){
    state.Imgs = {}
    state.Listloading = true
    const cof = {
      page:state.Page,
      pagesize:state.PageSize,
      sort:state.ListSort
    }
    getshejitulist(cof).then(res => {
      state.Imgs = res.data.imgs
      state.Count = res.data.count
      state.Listloading = false
    })
  }
}
