import {request} from "./request";

export function CreateParameter(data) {
    return request({
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        method: 'post',
        url: '/common/create/parameter',
        data: data
    })
}

export function GetParameter(data) {
    return request({
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        method: 'post',
        url: '/common/get/parameter',
        data: data
    })
}

export function SetParameter(data) {
    return request({
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        },
        method: 'post',
        url: '/common/set/parameter',
        data: data
    })
}